import React , {useState, useEffect} from 'react';
import'../App.css';
import Localbase from 'localbase'

const ToDo = () => {

    let db = new Localbase('db');
    db.config.debug = false;
    const img = 'images/logo.jpg';
    const [inputData, setInputData] = useState('');
    const [items, setItems] = useState([]);
    const [toggle, setToggle] = useState(false);
    const [Id, setId] = useState(null);
    const addItem = () =>{
         let a = inputData.trim();
        // console.log(inputData);
         if (a && toggle){
            setItems(
                items.map((element) =>{
                    if (element.id === Id){                        
                        db.collection('todo').doc({ id: Id }).update({
                            name: a
                          })
                        return {...element ,name : a};

                    }
                    return element;
                })
            ); 

                setToggle(false);
            }
        else if(a){
            console.log(a);
            const data = {id : new Date().getTime().toString(), name : a};
            setItems([...items , data]);
            db.collection('todo').add(data)
        }
        else {
            alert('Please Enter Data');
        }
        setInputData('');
    };

    const deleteItem = (id) => {
        db.collection('todo').doc({ id: id }).delete()
        const updatedItems = items.filter((element , index) =>{
                return element.id !== id ;
        }); 
        setItems(updatedItems)
    }

    const editItem = (id) => {
        setToggle(true);
        const editedItem = items.find((element) =>{
            return element.id === id;
        });
        setInputData(editedItem.name);
        setId(id);
    }
    
    async function getData() {
        try {
          let users = await db.collection('todo').get();
          setItems(users);
          console.log('users: ', users)
          return users;
        }
        catch(error) {
          console.log('error: ', error)
        }
      }
    useEffect(() => {
        getData();

        return () => {
            getData();
        }
    },[]);
    // getData();
    // setItems(data);

    return(
        <>
        <div className="main-div ">
            <div className="child-div">
                <figure>
                    <img src={img} alt="todoLogo" />
                    <figcaption>Add Your List Here 🤘</figcaption>
                </figure>
                <div className=" addItems">
                    <input type="text" value = {inputData}
                    onChange = {(e) =>setInputData(e.target.value)}
                     placeholder ='✍ Add Items...' 
                     onKeyPress = { 
                        (event)=>{ 
                        if(event.key === 'Enter'){ 
                            addItem(); 
                        }} 
                    }/>
                    {
                        toggle ?<i className = 'fas fa-edit add-btn' title = 'Update Item'
                                onClick ={addItem}></i>
                                : <i className = 'fa fa-plus add-btn' title = 'Add Item'
                                onClick ={addItem}></i>
                    }
                </div>
                <div className="showItems">
                   {
                       items.map((item) =>{
                        return(
                            <div className="eachItem" key={item.id}>
                                <h3>{item.name}</h3>
                               <div className="todo-btn">
                                    <i className="fas fa-edit add-btn" title='Edit Item'
                                        onClick = {() => editItem(item.id)}></i>
                                    <i className="fas fa-trash-alt add-btn" title='Delete Item' 
                                        onClick = {() => deleteItem(item.id)}></i>
                               </div>
                            </div>
                        )
                       })
                   }
                </div>
                {/* clear all <button></button> */}
                <div className="showItems">
                    <button className = 'btn effect04' data-sm-link-text = 'Remove All'
                    onClick ={ () => {setItems([]) ;db.collection('todo').delete()} }><span>
                        Clear List
                        </span></button>
                </div>
            </div>
        </div>
        </>
    )
}

export default ToDo;